<template>
  <v-card>
    <v-card-text>
      <v-card-subtitle class="headline"
        >Faturas
        <v-card-subtitle class="float-right">
          <v-icon class="mr-2"> mdi-currency-usd</v-icon>
          {{ formatDecimal(totalFaturado) }}
        </v-card-subtitle>
      </v-card-subtitle>

      <div>
        <v-row>
          <v-col cols="12" sm="12" xs="12" md="12"> </v-col>
        </v-row>
        <v-list>
          <v-list-item-group>
            <v-list-item>
              <v-row>
                <v-col>Código</v-col>
                <v-col>Criada em</v-col>
                <v-col>Baixada em</v-col>
                <v-col>Situação</v-col>
                <v-col class="text-right">Total</v-col>
              </v-row>
            </v-list-item>
            <template v-for="(item, index) in invoices">
              <v-list-item :key="item.id" @click="selectInvoice(item)">
                <v-row>
                  <v-col>{{ item.uid || "S/C/" }}</v-col>
                  <v-col>{{ formatDate(item.created.seconds) }}</v-col>
                  <v-col>{{
                    item.closed ? formatDate(item.created.seconds) : ""
                  }}</v-col>

                  <v-col>{{ item.status }}</v-col>
                  <v-col class="text-right"
                    >R$ {{ formatDecimal(item.totalValue) }}</v-col
                  >
                </v-row>
              </v-list-item>
              <v-divider v-if="index + 1 < invoices.length" :key="index">
              </v-divider>
            </template>
          </v-list-item-group>
        </v-list>

        <v-dialog
          v-model="confirmCloseInvoceDialog"
          max-width="50%"
          v-if="selectedInvoice != null"
        >
          <v-card>
            <v-card-title class="text-h5"> Baixar esta fatura? </v-card-title>

            <v-card-text>

                <p>Código da fatura: {{ selectedInvoice.uid || "l" }}</p>
                <p>
                  Valor da fatura: R$
                  {{ formatDecimal(selectedInvoice.totalValue || 0) }}
                </p>
                
              <div id=˜printSection˜>
                <invoice-print :orders="orders"></invoice-print>
              </div>
            </v-card-text>

            <v-card-actions>
              <v-menu bottom offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn class="ma-2 warning darken-1" v-bind="attrs" v-on="on">
                    Desmanchar
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    color="warning darken-1"
                    @click="onConfirmDestroyInvoice()"
                  >
                    <v-list-item-title>Confirmar</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>

              <v-spacer></v-spacer>
              <v-btn color="info darken-1" @click="onConfirmCloseInvoice()">
                Baixar
              </v-btn>

              <v-spacer></v-spacer>
              <v-btn color="secondary light-1" @click="printInvoiceOrders()">
                Imprimir
              </v-btn>

              <v-spacer></v-spacer>
              <v-btn
                color="default darken-1"
                @click="confirmCloseInvoceDialog = false"
              >
                Sair
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog
          v-model="confirmOpenInvoceDialog"
          max-width="290"
          v-if="selectedInvoice != null"
        >
          <v-card>
            <v-card-title class="text-h5"> Reabrir esta fatura? </v-card-title>

            <v-card-text>
              <p>Código da fatura: {{ selectedInvoice.uid || "l" }}</p>
              <p>
                Valor da fatura: R$
                {{ formatDecimal(selectedInvoice.totalValue || 0) }}
              </p>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="warning darken-1" @click="onConfirmOpenInvoice()">
                Reabrir Fatura
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn
                color="default darken-1"
                @click="confirmOpenInvoceDialog = false"
              >
                Cancelar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import InvoiceService from "../services/invoice-service";
import OrderService from "../services/order-service";
import moment from "moment";
import InvoicePrint from './InvoicePrint.vue';

export default {
  components: { InvoicePrint },
  
  name: "Invoice",

  props: ["customer"],

  data() {
    return {
      localCustomer: { id: null, name: null, phone: null, email: null },
      invoices: [],
      confirmCloseInvoceDialog: false,
      confirmOpenInvoceDialog: false,
      selectedInvoice: null,
      totalFaturado: 0,


      orders: [
      ],
    };
  },

  watch: {
    customer: function () {
      this.loadInvoices();
    },
  },

  created() {
    if (this.customer) this.loadInvoices();
  },

  methods: {
    async loadInvoices() {
      const invoiceService = new InvoiceService();
      this.invoices = await invoiceService.loadInvoices(this.customer.id);
      this.totalFaturado = 0;
      this.invoices.forEach((invoice) => {
        this.totalFaturado += invoice.totalValue;
      });
    },

    async selectInvoice(invoice) {
      if (invoice.status == "Aberta") {
        this.confirmCloseInvoceDialog = true;
        this.selectedInvoice = invoice;
      }
      if (invoice.status == "Baixada") {
        this.confirmOpenInvoceDialog = true;
        this.selectedInvoice = invoice;
      }

      const orderService = new OrderService();
      this.orders = [];
      this.orders = await orderService.loadOrdersByInvoice(invoice);
    },

    formatDate(value) {
      if (!value) return;
      return moment.unix(value).format("DD/MM/YYYY");
    },

    formatDecimal(value) {
      if (!value || value == undefined || isNaN(value)) {
        value = 0;
      }

      return value.toLocaleString("pt", {
        style: "decimal",
        minimumFractionDigits: "2",
      });
    },

    onConfirmCloseInvoice() {
      const invoiceService = new InvoiceService();
      invoiceService.closeInvoice(this.selectedInvoice);
      this.confirmCloseInvoceDialog = false;
    },

    onConfirmOpenInvoice() {
      const invoiceService = new InvoiceService();
      invoiceService.openInvoice(this.selectedInvoice);
      this.confirmOpenInvoceDialog = false;
    },

    async onConfirmDestroyInvoice() {
      const invoiceService = new InvoiceService();
      if (await invoiceService.destroyInvoice(this.selectedInvoice)) {
        this.selectedInvoice = null;
        this.loadInvoices();
      }
      this.confirmOpenInvoceDialog = false;
    },

    async printInvoiceOrders() {
      // Get HTML to print from element
      const prtHtml = document.getElementById('˜printSection˜').innerHTML;

      // Get all stylesheets HTML
      let stylesHtml = '';
      for (const node of [...document.querySelectorAll('link[rel="stylesheet"], style')]) {
        stylesHtml += node.outerHTML;
      }

      // Open the print window
      const WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');

      WinPrint.document.write(`<!DOCTYPE html>
      <html>
        <head>
          ${stylesHtml}
        </head>
        <body>
          ${prtHtml}
        </body>
      </html>`);

      WinPrint.document.close();
      WinPrint.focus();
      WinPrint.print();
      WinPrint.close();
    },
  },
};
</script>