 import firebase from "firebase";
 import OrderService from "./order-service";

export default class InvoiceService{


    async destroyInvoice(invoice){
        
        let invoiceRef = firebase.firestore().collection("invoice").doc(invoice.id);

        const snapshot = await firebase
        .firestore()
        .collection("order")
        .where("invoice", "==" , invoiceRef).get();

        let orders =  snapshot.docs.map(doc=>doc.data());

        await orders.forEach(async order=>{
            
            
            order.invoice = firebase.firestore.FieldValue.delete();
            order.invoiceUid = firebase.firestore.FieldValue.delete();
            order.billingStatus = "Aberto";

            await firebase
             .firestore()
             .collection("order")
             .doc(order.id)
             .set(order, { merge: true });
        })

        await invoiceRef.delete();
        invoice = null;
        return true;
    }


    async makeInvoice(ordersId, customerId){

        const orders = await this.getOrders(ordersId);


        const invoice = {totalValue:0, uid:(Math.round(Date.now() / 1000))};

        orders.forEach(order=>{

            if(order.billingStatus =='Aberto'){
                invoice.totalValue += order.price;
                order.billingStatus = 'Faturado';
            }
        })


        invoice.customer = firebase
        .firestore()
        .doc("/customer/" + customerId);

        invoice.created = new Date();
        invoice.status = 'Aberta';


        const inv= await firebase
        .firestore()
        .collection("invoice")
        .add(invoice);


        orders.forEach(async order => {
            order.invoice = inv
            order.invoiceUid = invoice.uid;
           await firebase
            .firestore()
            .collection("order")
            .doc(order.id)
            .set(order, { merge: true });
        });
       return(await inv.get()).data();
        
    }


    async loadInvoices(customerId){
        let custRef = firebase.firestore().collection("customer").doc(customerId);
        const snapshot = await firebase
        .firestore()
        .collection("invoice")
        .where("customer", "==", custRef).get();

        return snapshot.docs.map(doc=>
            {
                let invoice = doc.data();
                invoice.id = doc.id;
                return invoice;
            });
    }

    async getOrders(ordersId){
        const snapshot = await firebase
        .firestore()
        .collection("order")
        .where(firebase.firestore.FieldPath.documentId(), "in" , ordersId).get();


        return  snapshot.docs.map(doc=>{
            const orderData = doc.data()
            orderData.id = doc.id;
            return orderData;
        });
        
    }

    async closeInvoice(invoice){
        invoice.status = 'Baixada';
        invoice.closed = new Date();

        firebase
        .firestore()
        .collection("invoice")
        .doc(invoice.id)
        .set(invoice, { merge: true });

        const orderService = new OrderService();
        const orders = await orderService.loadOrdersByInvoice(invoice);
        orders.forEach(order=>{
            order.billingStatus = 'Pago';
            firebase
            .firestore()
            .collection("order")
            .doc(order.id)
            .set(order, { merge: true });
        })

    }

    async openInvoice(invoice){
        invoice.status = 'Aberta';
        invoice.closed = new Date();

        firebase
        .firestore()
        .collection("invoice")
        .doc(invoice.id)
        .set(invoice, { merge: true });

        const orderService = new OrderService();
        const orders = await orderService.loadOrdersByInvoice(invoice);
        orders.forEach(order=>{
            order.billingStatus = 'Faturado';
            firebase
            .firestore()
            .collection("order")
            .doc(order.id)
            .set(order, { merge: true });
        })


    }
}