import firebase from "firebase";


export default class OrderService{


    async loadOrdersByInvoice(invoice){
        
        let invoiceRef = firebase.firestore().collection("invoice").doc(invoice.id);

        const snapshot = await firebase
        .firestore()
        .collection("order")
        .where("invoice", "==" , invoiceRef).get();

		return  await snapshot.docs.map(doc=>doc.data());

	}
}